
import { Options, Vue } from "vue-class-component";
import Card from "@/components/Card.vue";
import ToolCard from "@/components/ToolCard.vue";
import Button from "@/components/Button.vue";
import TempChart from "@/components/TempChart.vue";
import { DataStore } from "../services/data.store";
import { differenceInSeconds } from "date-fns";

@Options({
  components: {
    Card,
    Button,
    ToolCard,
    TempChart,
  },
})
export default class Home extends Vue {
  public sensorsState = {
    temperature: { value: 0 },
    light: { status: -1 },
    nightLight: { status: -1 },
    filter: { status: -1 },
    heater: { status: -1 },
    feeding: { status: -1 },
  };
  public configData = {
    feedingIntervalCroneCmd: "",
    filterSwitchingOffIntervalCroneCmd: "",
    filterSwitchingOnIntervalCroneCmd: "",
    lightSwitchingOffIntervalCroneCmd: "",
    lightSwitchingOnIntervalCroneCmd: "",   
    nightLightSwitchingOffIntervalCroneCmd: "",
    nightLightSwitchingOnIntervalCroneCmd: "",
    maxTemperature: "",
    minTemperature: "",
    tempReadIntervalCroneCmd: "",
    updateStateIntervalCroneCmd: "",
  };
  private subs: any[] = [];
  private fromLastRefreshCounter = 0;
  public tempChartData = {};

  public getFromLastRefreshCounter() {
    return this.fromLastRefreshCounter;
  }

  mounted() {
    this.subs.push(
      DataStore.sensorsState.subscribe((data) => {
        this.sensorsState = data;
      })
    );

    this.subs.push(
      DataStore.configData.subscribe((data) => {
        this.configData = data;
        this.fromLastRefreshCounter = differenceInSeconds(
          new Date(
            DataStore.getParseCroneCmdNextDate(
              this.configData.updateStateIntervalCroneCmd
            )
          ),
          new Date()
        );
      })
    );

    this.subs.push(
      DataStore.tempChartData.subscribe((data) => {
        this.tempChartData = data;
      })
    );

    setInterval(() => {
      this.fromLastRefreshCounter--;
    }, 1000);

    DataStore.getTempChartData();
  }

  beforeUnmount() {
    this.subs.forEach((sub) => sub.unsubscribe());
  }
}
