
import { subHours } from "date-fns";
import { differenceInHours } from "date-fns/fp";
import { Vue } from "vue-class-component";
import { DataStore } from "../services/data.store";

export default class Header extends Vue {
  public appSocketStatus: {
    backendConnected: boolean;
    rpiConnected: boolean;
  } = {
    backendConnected: false,
    rpiConnected: false,
  };

  public sensorsState = {
    temperature: { value: 0 },
    light: { status: -1 },
    filter: { status: -1 },
    heater: { status: -1 },
    feeding: { status: -1 },
  };
  public configData = {};
  private subs: any[] = [];

  public getTimeFromLastUpdate() {
    return differenceInHours(subHours(Date.now(), 2), Date.now());
  } 
  
  public logout() {
    localStorage.removeItem('authToken');
    window.location.reload();
  }

  mounted() {
    this.subs.push(
      DataStore.appSocketState.subscribe((data) => {
        this.appSocketStatus = data;
      })
    );

    this.subs.push(
      DataStore.sensorsState.subscribe((data) => {
        this.sensorsState = data;
      })
    );

    this.subs.push(
      DataStore.configData.subscribe((data) => {
        this.configData = data;
      })
    );

    DataStore.getSensorsState();
    DataStore.getConfigData();
    DataStore.getRPISystemStatus();
  }

  beforeUnmount() {
    this.subs.forEach((sub) => sub.unsubscribe());
  }
}
