
import { Options, Vue } from "vue-class-component";
import Button from "@/components/Button.vue";
import { Prop } from "vue-property-decorator";

@Options({
  components: {
    Button,
  },
  emits: ["onHide", "onSave"],
})
export default class Modal extends Vue {
  @Prop() submitBtnText = '';
  @Prop() submitBtnIcon = 'fas fa-save';
  @Prop() title = '';
  @Prop() show = '';

  private hideModal() {
    this.$emit("onHide");
  }

  private save() {
    this.$emit("onSave");
  }
}
