
import * as croneParser from "cron-parser";
import { format } from "date-fns";
import { Options, Vue } from "vue-class-component";
import Card from "./Card.vue";
import Button from "./Button.vue";
import Modal from "@/components/Modal.vue";
import { DataStore } from "../services/data.store";
import { Prop } from "vue-property-decorator";
import { vibrate } from "../services/utils";

@Options({
  components: {
    Card,
    Button,
    Modal,
  },
  computed: {
    isEditionMode: false,
    computedStatus: -1,
  },
  emits: ["onEdit"],
})
export default class ToolCard extends Vue {
  @Prop() title = "";
  @Prop() type = undefined;
  @Prop() lastUpdate = Date.now();
  @Prop() btnText = undefined;
  @Prop() status = -1;
  @Prop() inputData = {
    onCroneCmd: "",
    offCroneCmd: "",
  };

  public editedCroneCmd = {
    onCroneCmd: "",
    offCroneCmd: "",
  };
  private isEditionModeComp = false;
  public showEditionModal = false;
  public showConfirmModal = false;

  public get isEditionMode() {
    return this.isEditionModeComp;
  }

  public get computedStatus() {
    return this.status;
  }

  public get lastUpdateTime() {
    if (this.lastUpdate) {
      return format(new Date(this.lastUpdate), "HH:mm:ss, dd.MM.yyy");
    } else {
      return "";
    }
  }

  public getParseCroneCmd(croneCmd: string) {
    try {
      if (croneCmd && croneCmd.length > 5) {
        return format(
          new Date(croneParser.parseExpression(croneCmd).next().toString()),
          "HH:mm:ss, dd.MM.yyy"
        );
      } else {
        return croneCmd;
      }
    } catch (err) {
      return "error! cant parse crone cmd";
    }
  }

  public getParseCroneCmdNextEvents(
    croneCmd: string,
    howManyNextEvents: number = 1
  ) {
    try {
      if (croneCmd && croneCmd.length > 5) {
        let eventDates = "";
        let iterator = croneParser.parseExpression(croneCmd);

        for (let i = howManyNextEvents; i > 0; i--) {
          eventDates += `${format(
            new Date(iterator.next().toString()),
            "HH:mm:ss, dd.MM.yyy"
          )} ${i !== 1 ? ", " : ""}`;
        }

        return eventDates;
      } else {
        return croneCmd;
      }
    } catch (err) {
      return "error! cant parse crone cmd";
    }
  }

  public editCroneCmd(inputData: any) {
    vibrate();
    this.editedCroneCmd.onCroneCmd = inputData.onCroneCmd;
    this.editedCroneCmd.offCroneCmd = inputData.offCroneCmd;
    this.showEditionModal = true;
  }

  public saveCroneCmdEdition(dataType: any) {
    DataStore.saveCroneCmdEdition(dataType, this.editedCroneCmd);
  }

  public toggleItem() {
    vibrate();
    this.showConfirmModal = true;
  }

  public onEditionModalSave(dataType: string) {
    this.saveCroneCmdEdition(dataType);
    this.showEditionModal = false;
  }

  public onEditionModalHide() {
    this.showEditionModal = false;
  }

  public onConfirmModalHide() {
    this.showConfirmModal = false;
  }

  public onConfirmModalSave(dataType: any) {
    DataStore.toggleItem(dataType);
    this.showConfirmModal = false;
  }
}
