
import { Options, Vue } from "vue-class-component";
import Header from "@/components/Header.vue"; // @ is an alias to /src
import Footer from "@/components/Footer.vue";
import Toast from "@/components/Toast.vue";
import { initSockets } from "./services/socets.service";
import { ApiService } from "./services/api.service";
import { vibrate } from "./services/utils";
import { ToastCtrl } from "./services/utils";
import { getCurrentInstance } from 'vue'

import { getMessaging, getToken } from "firebase/messaging";

@Options({
  components: {
    Header,
    Footer,
    Toast,
  },
})
export default class App extends Vue {
  public loggedIn = false;
  public error = false;
  public loginBoxes: number[] = [0, 1, 2, 3, 4, 5, 6, 7, 8];
  public clickedBoxes: number[] = [];
  public toastData = {
    show: false,
    text: "",
    type: "",
  };

  public registerClickOnBox(boxNumber: number) {
    vibrate();
    this.error = false;
    this.clickedBoxes.push(boxNumber);

    if (this.clickedBoxes.length === 4) {
      ApiService.checkPassword(this.clickedBoxes.toString())
        .then((resonse: any) => {
          this.loggedIn = resonse.isLoggedIn;
          localStorage.setItem("authToken", resonse.token);
          ToastCtrl.showToast("Login success");
        })
        .catch(() => {
          this.loggedIn = false;
          this.clickedBoxes = [];
          this.error = true;
          vibrate([400, 0, 400]);
        });
    }
  }

  public isClicked(boxNumber: number) {
    return this.clickedBoxes.includes(boxNumber);
  }

  public initToast() {
    ToastCtrl.toast.subscribe(
      (toastConfig: { text: string; timeout: number, action: string, type: string }) => {
        this.toastData.show = true;
        this.toastData.text = toastConfig.text;
        this.toastData.type = toastConfig.type;

        setTimeout(() => {
          this.toastData.show = false;
          this.toastData.text = "";
        }, toastConfig.timeout);
      }
    );
  }

  public requestPermission() {
    console.log('Requesting permission...');
    Notification.requestPermission().then((permission) => {
      if (permission === 'granted') {
        console.log(permission)
        console.log('Notification permission granted.');
      }
    });
  }

  mounted() {
    initSockets();
    this.initToast();

    this.requestPermission();

    const app = getCurrentInstance();

    if (app) {
      //const firebaseMessaging = app.appContext.config.globalProperties.$messaging;

      const messaging = getMessaging();

      getToken(messaging, { vapidKey: 'BAcluGy0fnYEL97NxQvukyI9CZl6XLiIt6NXUKsIthqYAtbYmvF1NnJCO75iXTU-dAFuaBWeYzjXQ9BmIEfGXhg' }).then((currentToken) => {
        if (currentToken) {
          console.log('currentToken', currentToken);
 

          ApiService.registerFirebaseToken(currentToken, navigator.userAgent)
            .then(() => {
              ToastCtrl.showToast("Firebase token registered");
            })
            .catch(() => {
              ToastCtrl.showErrorToast("Firebase token not registered");
            });


        } else {
          // Show permission request UI
          console.log('No registration token available. Request permission to generate one.');
          // ...
        }
      }).catch((err) => {
        console.log('An error occurred while retrieving token. ', err);
        // ...
      });

    }


    if (localStorage.getItem("authToken")) {
      this.loggedIn = true;
    }
  }
}
