import { Subject } from "rxjs";
export const HOST_URL = process.env.VUE_APP_HOST_URL;

const Navigator = window.navigator;

export function vibrate(pattern: number | Array<number> = 100) {
  Navigator.vibrate(pattern);
}

export class ToastCtrl {
  private static _toast = new Subject<any>();

  public static get toast() {
    return this._toast.asObservable();
  }

  public static showToast(text: string) {
    this._toast.next({text: text, timeout: 3000, action: 'OPEN', type: "SUCCESS"}); 
  } 
  
  public static showErrorToast(text: string) {
    this._toast.next({text: text, timeout: 5000, action: 'OPEN', type: 'ERROR'}); 
  }
}