import axios from 'axios';
import { ToastCtrl } from './utils';
import { HOST_URL } from '../services/utils';

export class ApiService {
  public feedingPending = false;

  constructor() { }

  static getTokenHeader() {
    return {
      'Authorization': 'Bearer ' + localStorage.getItem('authToken')
    };
  }
  
  static handleError(err: any) {
    if(err.response.status === 401) {
      localStorage.removeItem('authToken');
      setTimeout(() => {
        window.location.reload();
      }, 5000);
      ToastCtrl.showErrorToast('Authorization error or token expired!');
    }
  }

  static getRPISystemStatus() {
    return new Promise((resolve, reject) => {
      axios.get(`${HOST_URL}/api/system`, { headers: this.getTokenHeader()}).then((response) => {
        resolve(response.data);
      }).catch((err) => {
        this.handleError(err);
        reject(err);
      });
    });
  }

  static getTempHistory() {
    return new Promise((resolve, reject) => {
      axios.get(`${HOST_URL}/api/temperatures`, { headers: this.getTokenHeader()}).then((response) => {
        resolve(response.data);
      }).catch((err) => {
        this.handleError(err);
        reject(err);
      });
    });
  }

  static getSensorsData() {
    return new Promise((resolve, reject) => {
      axios.get(`${HOST_URL}/api/sensors`, { headers: this.getTokenHeader()}).then((response) => {
        resolve(response.data);
      }).catch((err) => {
        this.handleError(err);
        reject(err);
      });
    });
  }


  static getConfigData() {
    return new Promise((resolve, reject) => {
      axios.get(`${HOST_URL}/api/config`, { headers: this.getTokenHeader()}).then((response) => {
        resolve(response.data);
      }).catch((err) => {
        this.handleError(err);
        reject(err);
      });
    });
  }

  static setConfigData(configData: any) {
    return new Promise((resolve, reject) => {
      axios.post(`${HOST_URL}/api/config`, configData, { headers: this.getTokenHeader()}).then((response) => {
        resolve(response.data);
      }).catch((err) => {
        this.handleError(err);
        reject(err);
      });
    });
  }

  static feed() {
    return new Promise((resolve, reject) => {
      axios.put(`${HOST_URL}/api/sensors/feeding`, { feed: true }, { headers: this.getTokenHeader()}).then((response) => {
        resolve(response.data);
      }).catch((err) => {
        this.handleError(err);
        reject(err);
      });
    });
  }

  static toggleNightLight(newNightLightState: number) {
    return new Promise((resolve, reject) => {
      axios.put(`${HOST_URL}/api/sensors/night-light`, { newLightState: newNightLightState }, { headers: this.getTokenHeader()}).then((response) => {
        resolve(response.data);
      }).catch((err) => {
        this.handleError(err);
        reject(err);
      });
    });
  } 
  
  static toggleLight(newLightState: number) {
    return new Promise((resolve, reject) => {
      axios.put(`${HOST_URL}/api/sensors/light`, { newLightState: newLightState }, { headers: this.getTokenHeader()}).then((response) => {
        resolve(response.data);
      }).catch((err) => {
        this.handleError(err);
        reject(err);
      });
    });
  }

  static toggleHeater(newHeaterState: number) {
    return new Promise((resolve, reject) => {
      axios.put(`${HOST_URL}/api/sensors/heater`, { newHeaterState: newHeaterState }, { headers: this.getTokenHeader()}).then((response) => {
        resolve(response.data);
      }).catch((err) => {
        this.handleError(err);
        reject(err);
      });
    });
  }

  static toggleFilter(newFilterState: number) {
    return new Promise((resolve, reject) => {
      axios.put(`${HOST_URL}/api/sensors/filter`, { newFilterState: newFilterState }, { headers: this.getTokenHeader()}).then((response) => {
        resolve(response.data);
      }).catch((err) => {
        this.handleError(err);
        reject(err);
      });
    });
  }
  
  static checkPassword(password: string) {
    return new Promise((resolve, reject) => {
      axios.post(`${HOST_URL}/api/auth`, { password: password }).then((response) => {
        resolve(response.data);
      }).catch((err) => {
        reject(err);
      });
    });
  }  
  
  static registerFirebaseToken(token: string, userAgent: string) {
    return new Promise((resolve, reject) => {
      axios.post(`${HOST_URL}/api/register-token`, { token, userAgent }).then((response) => {
        resolve(response.data);
      }).catch((err) => {
        reject(err); 
      });
    });
  }
}
