import { io } from 'socket.io-client';
import { DataStore } from '../services/data.store';
import { HOST_URL } from '../services/utils';

export function initSockets() {
    const socket = io(HOST_URL);

    socket.on('connect', function () {
        DataStore.setBackendConnectionStatus(true);
        socket.emit('appConnected');
    });

    socket.on('refreshApp', function () {
        console.log('refresh from socket');
        DataStore.getSensorsState();
        DataStore.getConfigData();
        DataStore.getTempChartData();
    });

    socket.on('feeding', function () {
        console.log('feeding');
    });

    socket.on('disconnect', function () {
        socket.emit('appDisconnected');
    });

}
