import { BehaviorSubject } from 'rxjs';
import { ApiService } from "./api.service";
import { format } from "date-fns";
import * as croneParser from "cron-parser";
import { ToastCtrl } from './utils';

export enum DATA_TYPES {
  FEEDING = "FEEDING",
  DAY_LIGHT = "DAY_LIGHT",
  NIGHT_LIGHT = "NIGHT_LIGHT",
  HEATER = "HEATER",
  FILTER = "FILTER",
  REFRESH = "REFRESH"
}
export interface SensorsState {
  lastUpdateTime: Date,
  temperature: { value: number },
  light: { status: number },
  nightLight: { status: number },
  filter: { status: number },
  heater: { status: number },
  feeding: { status: number }
}

export interface ConfigData {
  feedingIntervalCroneCmd: string,
  filterSwitchingOffIntervalCroneCmd: string,
  filterSwitchingOnIntervalCroneCmd: string,
  lightSwitchingOffIntervalCroneCmd: string,
  lightSwitchingOnIntervalCroneCmd: string,  
  nightLightSwitchingOffIntervalCroneCmd: string,
  nightLightSwitchingOnIntervalCroneCmd: string,
  maxTemperature: string,
  minTemperature: string,
  tempReadIntervalCroneCmd: string,
  updateStateIntervalCroneCmd: string
}

const state = {
  appSocketStatus: new BehaviorSubject<{
    backendConnected: boolean,
    rpiConnected: boolean
  }>({
    backendConnected: false,
    rpiConnected: false
  }),

  sensorsState: new BehaviorSubject<SensorsState>({
    lastUpdateTime: new Date(),
    temperature: { value: 0 },
    light: { status: -1 },
    nightLight: { status: -1 },
    filter: { status: -1 },
    heater: { status: -1 },
    feeding: { status: -1 }
  }),

  configData: new BehaviorSubject<ConfigData>({
    feedingIntervalCroneCmd: "",
    filterSwitchingOffIntervalCroneCmd: "",
    filterSwitchingOnIntervalCroneCmd: "",
    lightSwitchingOffIntervalCroneCmd: "",
    lightSwitchingOnIntervalCroneCmd: "", 
    nightLightSwitchingOffIntervalCroneCmd: "",
    nightLightSwitchingOnIntervalCroneCmd: "",
    maxTemperature: "",
    minTemperature: "",
    tempReadIntervalCroneCmd: "",
    updateStateIntervalCroneCmd: ""
  }),

  tempChartData: new BehaviorSubject<{
    temperatures: number[],
    dates: Date[]
  }>({
    temperatures: [],
    dates: []
  }),

  request: new BehaviorSubject<{
    message: string,
    isPending: boolean
  }>({
    message: '',
    isPending: false
  })
};

export class DataStore {

  constructor() {

  }

  public static get appSocketState() {
    return state.appSocketStatus.asObservable()
  }

  public static setRpiConnectionStatus(rpiSocketStatus: boolean) {
    state.appSocketStatus.next({ ...state.appSocketStatus.getValue(), rpiConnected: rpiSocketStatus });
  }

  public static setBackendConnectionStatus(backendSocketStatus: boolean) {
    state.appSocketStatus.next({ ...state.appSocketStatus.getValue(), backendConnected: backendSocketStatus });
  }

  public static getRPISystemStatus() {
    this.setRequestPending('getting RPI system status...', true);
    ApiService.getRPISystemStatus().then((data: any) => {
      this.setRequestPending('', false);
      if (data && data.status) {
        this.setRpiConnectionStatus(true);
        ToastCtrl.showToast('RPI connected!');
      } else {
        this.setRpiConnectionStatus(false);
        ToastCtrl.showErrorToast('RPI not connected!');
      }
    });
  }


  public static get sensorsState() {
    return state.sensorsState.asObservable();
  }

  public static getSensorsState() {
    this.setRequestPending('getting sensors state...', true);
    ApiService.getSensorsData().then((data: any) => {
      this.setRequestPending('', false);
      const sensorsData = data.sensors;
      state.sensorsState.next({
        lastUpdateTime: sensorsData.lastUpdateTime,
        temperature: { value: sensorsData.temperature.value },
        light: { status: sensorsData.light.status },
        nightLight: { status: sensorsData.nightLight.status },
        filter: { status: sensorsData.filter.status },
        heater: { status: sensorsData.heater.status },
        feeding: { status: sensorsData.feeding.status }
      });
    })
  }

  public static setSensorsState(sensorsCurrentState: SensorsState) {
    state.sensorsState.next({ ...state.sensorsState.getValue(), ...sensorsCurrentState });
  }

  public static get configData() {
    return state.configData.asObservable();
  }

  public static getConfigDataValue(): ConfigData {
    return state.configData.getValue();
  }

  public static getConfigData() {
    this.setRequestPending('getting config data...', true);
    ApiService.getConfigData().then((data: any) => {
      this.setRequestPending('', false);
      state.configData.next(data);
    });
  }

  public static get tempChartData() {
    return state.tempChartData.asObservable();
  }

  public static getTempChartData() {
    this.setRequestPending('getting temperature history...', true);
    ApiService.getTempHistory().then((data: any) => {
      this.setRequestPending('', false);
      const mappedData = {
        temperatures: data.lastTemperatureValues.map((item: any) => item.value),
        dates: data.lastTemperatureValues.map((item: any) => format(new Date(item.date), 'HH:mm'))
      }
      state.tempChartData.next(mappedData);
    });
  }


  public static get request() {
    return state.request.asObservable();
  }

  public static setRequestPending(message: string, isPending: boolean) {
    state.request.next({ message, isPending });
  }


  public static saveCroneCmdEdition(dataType: DATA_TYPES, editedData: any) {
    const editedConfigData = { ...state.configData.getValue() };

    switch (dataType) {
      case DATA_TYPES.FEEDING:
        editedConfigData.feedingIntervalCroneCmd = editedData.onCroneCmd;
        break;
      case DATA_TYPES.REFRESH:
        editedConfigData.updateStateIntervalCroneCmd = editedData.onCroneCmd;
        break;
      case DATA_TYPES.DAY_LIGHT:
        editedConfigData.lightSwitchingOnIntervalCroneCmd = editedData.onCroneCmd;
        editedConfigData.lightSwitchingOffIntervalCroneCmd = editedData.offCroneCmd;
        break; 
      case DATA_TYPES.NIGHT_LIGHT:
        editedConfigData.nightLightSwitchingOnIntervalCroneCmd = editedData.onCroneCmd;
        editedConfigData.nightLightSwitchingOffIntervalCroneCmd = editedData.offCroneCmd;
        break;
      case DATA_TYPES.HEATER:
        editedConfigData.minTemperature = editedData.onCroneCmd;
        editedConfigData.maxTemperature = editedData.offCroneCmd;
        break;
      case DATA_TYPES.FILTER:
        editedConfigData.filterSwitchingOnIntervalCroneCmd = editedData.onCroneCmd;
        editedConfigData.filterSwitchingOffIntervalCroneCmd = editedData.offCroneCmd;
        break;
    }

    this.setRequestPending(`setting new ${dataType} crone cmd...`, true);
    ApiService.setConfigData(editedConfigData).then((data: any) => {
      this.setRequestPending('', false);
      state.configData.next(editedConfigData);
      ToastCtrl.showToast('Edit complete!');
    });
  }

  public static toggleItem(dataType: DATA_TYPES) {
    const editedSensorsState = { ...state.sensorsState.getValue() };

    switch (dataType) {
      case DATA_TYPES.REFRESH:
        this.setRequestPending(`refreshing data...`, true);
        this.getTempChartData();
        this.getConfigData();
        this.getSensorsState();
        this.getRPISystemStatus();
        ToastCtrl.showToast('Data refreshed!');
        break;
      case DATA_TYPES.FEEDING:
        this.setRequestPending(`feeding...`, true);
        ApiService.feed().then((data: any) => {
          this.setRequestPending('', false);
          ToastCtrl.showToast('Feeding complete!');
        });
        break;
      case DATA_TYPES.DAY_LIGHT:
        // eslint-disable-next-line no-case-declarations
        const newLightState = editedSensorsState.light.status ? 0 : 1;
        this.setRequestPending(`setting light to ${newLightState ? 'ON' : 'OFF'}...`, true);
        ApiService.toggleLight(newLightState).then((data: any) => {
          this.setRequestPending('', false);
          editedSensorsState.light.status = newLightState;
          state.sensorsState.next(editedSensorsState);
          newLightState? ToastCtrl.showToast('Day light turned on!') : ToastCtrl.showToast('Day light turned off!');
        });
        break;  
      case DATA_TYPES.NIGHT_LIGHT:
        // eslint-disable-next-line no-case-declarations
        const newNightLightState = editedSensorsState.nightLight.status ? 0 : 1;
        this.setRequestPending(`setting night light to ${newNightLightState ? 'ON' : 'OFF'}...`, true);
        ApiService.toggleNightLight(newNightLightState).then((data: any) => {
          this.setRequestPending('', false);
          editedSensorsState.nightLight.status = newNightLightState;
          state.sensorsState.next(editedSensorsState);
          newNightLightState? ToastCtrl.showToast('Night light turned on!') : ToastCtrl.showToast('Night light turned off!');
        });
        break;
      case DATA_TYPES.HEATER:
        // eslint-disable-next-line no-case-declarations
        const newHeaterState = editedSensorsState.heater.status ? 0 : 1;
        this.setRequestPending(`setting heater to ${newHeaterState ? 'ON' : 'OFF'}...`, true);
        ApiService.toggleHeater(newHeaterState).then((data: any) => {
          this.setRequestPending('', false);
          editedSensorsState.heater.status = newHeaterState;
          state.sensorsState.next(editedSensorsState);
          newHeaterState? ToastCtrl.showToast('Heater turned on!') : ToastCtrl.showToast('Heater turned off!');
        });
        break;
      case DATA_TYPES.FILTER:
        // eslint-disable-next-line no-case-declarations
        const newFilterState = editedSensorsState.filter.status ? 0 : 1;
        this.setRequestPending(`setting filter to ${newFilterState ? 'ON' : 'OFF'}...`, true);
        ApiService.toggleFilter(newFilterState).then((data: any) => {
          this.setRequestPending('', false);
          editedSensorsState.filter.status = newFilterState;
          state.sensorsState.next(editedSensorsState);
          newFilterState? ToastCtrl.showToast('Filter turned on!') : ToastCtrl.showToast('Filter turned off!');
        });
        break;
    }

  }

  public static getParseCroneCmd(croneCmd: string): string {
    if (croneCmd && croneCmd.length > 4) {
      return format(
        new Date(croneParser.parseExpression(croneCmd).next().toString()),
        "HH:mm:ss, dd.MM.yyy"
      );
    } else {
      return croneCmd;
    }
  }

  public static getParseCroneCmdNextDate(croneCmd: string): Date {
    if (croneCmd && croneCmd.length > 4) {
      return new Date(croneParser.parseExpression(croneCmd).next().toString());
    } else {
      return new Date();
    }
  }
}